<template>
  <div class="page-container">
    <div class="topbar">
      <h1>Blog Posts</h1>
    </div>
    <div class="container">
      <!-- <h1>Blog Posts</h1> -->
      
      <div class="create-post">
        <label for="create-post"><strong>Write a blog post: </strong></label>
        <input type="text" id="create-post" v-model="text" placeholder="Write here...">
        <button v-on:click="createPost">Post!</button>
      </div>

      <hr>
      <p class = "error" v-if="error">
        {{error}}
      </p>
      
      <div class="posts-container">
        <div class="post" 
          
          @mouseover="hover = true"
          @mouseleave="hover = false"
          v-for="(post,index) in posts"
          v-bind:item="post"
          v-bind:index="index"
          v-bind:key="post._id"
          v-on:dblclick="deletePost(post._id)"
        >
          {{ `${post.createdAt.getMonth()}/${post.createdAt.getDate()}/${post.createdAt.getFullYear()}` }}
          <p class="text">{{ post.text }}</p>
          <br>
          <button v-on:click="deletePost(post._id)">Delete</button>
          
          <!-- <p>
            <font-awesome-icon icon="trash" />
          </p> -->
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'; 

export default {
  name: 'PostComponent',
  //data - like component state
  data() {
    return {
      posts: [], 
      error: '', 
      text: '',
      hover: false
    }
  }, 
  //runs automatically when component intialized
  async created() {
    try {
      this.posts = await PostService.getPosts(); 
    } catch(err) {
      this.error = err.message; 
    }
  }, 
  methods: {
    async createPost() {
      await PostService.insertPost(this.text); 
      this.$toast.success(`Post created successfully`, {
         position: "top-right",
         duration:2000
      });
      this.text = '';
      this.posts = await PostService.getPosts(); 
    }, 
    async deletePost(id) {
      await PostService.deletePost(id); 
      this.$toast.success(`Post successfully deleted`, {
         position: "top-right",
         duration:2000
      });
      this.posts = await PostService.getPosts(); 
    }, 
  }
}
</script>

